import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import {
    BrowserRouter as Router,
    Route,
    Link
} from 'react-router-dom'
import fetch from 'node-fetch'


class Home extends Component {
  render() {
    return (
      <div className="App">
        <div className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h2>Welcome to React</h2>
        </div>
        <p className="App-intro">
          To get started, edit <code>src/App.js</code> and save to reload.
        </p>
      </div>
    );
  }
}

class Board extends Component {
    renderSquare(i) {
        return <Square value={i} />
    }
}

class Square extends Component {
    render() {
        return (
            <button className="square">
                {this.props.value}
            </button>
        )
    }
}

class Upload extends Component {
    render() {
        const body = JSON.parse('[{ "unixtime": 1496536003990, "timestamp": "2017-06-04_09:26:43", "longitude": 35.7502211, "latitude": 139.8147858, "altitude": 38.0, "accuracy": 3.0, "pressure": 1007.46686, "theta": 149.23602, "zengokeisya": 11.477978, "sayukeisya": -4.062758 }]');
        fetch('https://rocky-woodland-39339.herokuapp.com/api/upload', {
            method: 'POST',
            body: JSON.stringify(body),
            headers: {
                'Content-Type': 'application/json',
            },
        })
            .then(res => res.json())
            .then(json => console.log('result: ' + JSON.stringify(json)));


        return (
            <div className="App">
                <input type="file" name="image_file[]" multiple="multiple" />
                <p>test</p>
            </div>
        )
    }
}

const MyRouter = () => (
    <Router>
        <div>
            <ul>
                <li><Link to="/">Home</Link></li>
                <li><Link to="/board">Board</Link></li>
                <li><Link to="/upload">Upload</Link></li>
            </ul>

            <hr/>

            <Route exact path="/" component={Home}/>
            <Route path="/about" component={Board}/>
            <Route path="/upload" component={Upload}/>
        </div>
    </Router>
);


export default MyRouter;



